<template lang="pug">

  .favourite-contact.calls-fixed-height
    .header
      .nav
        button(@click='edit').nav__option {{editor ? "Готово" : "Править"}}
      h1.header__title Избранные
    

    FavouriteItem(:isEdit='editor')


</template>

<script>

  import FavouriteItem from './FavouriteItem'

  export default {
    components: {
      FavouriteItem
    },
    data() {
      return {
        editor: false
      }
    },
    methods: {
      edit() {
        return this.editor = !this.editor
      }
    }
  }

</script>

<style lang="sass">

  @import '../../assets/sass/phone/apps/calls/favourite'

</style>