<template lang="pug">
  
  .favourite-list
    p(v-if='!favouriteList.length').phone__info Нет избранных

    .favourite-item(v-else v-for='(item, index) in favouriteList')

      router-link(:to="{name: 'ContactInfo', params: {phone: item.number}}")
        button(v-if='isEdit' @click='deleteFromFavourite($event, item)').calls__delete
        .favourite-contact-person
          .favourite-contact-person__picture {{item.name[0]}}
          h1.favourite-contact-person__name {{item.name}}

</template>

<script>

  import {mapState} from 'vuex'

  export default {
    props: {
      isEdit: Boolean,
      default: false
    },
    computed: {
      ...mapState(['calls']),

      favouriteList() {
        return this.calls.contacts.filter(item => item.favourite);
      }
    },
    methods: {
      deleteFromFavourite(e, user) {
        e.preventDefault();        

        this.$store.commit('CHANGE_USER_FAVOURITE', {number: user.number, action: false});

        if (!this.favouriteList.length) 
          this.$alert.show('Список избранных очищен') 
      }
    }
  }

</script>

